html,body {
    margin:0 ;
    padding:0;
    overflow-x:hidden;
}
.navbar {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding-top: .5rem;
    padding-bottom: .5rem;
    /* padding-left: 100px;
    padding-right: 100px; */
}
.logo{
    width: 120px;
    height: 70px;
    border-radius: 10px;
    
}


.navbar-nav {
    display: flex;
    flex-direction: column;
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
    margin: auto;
}

